import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddComponent } from './components/views/add/add.component';
import { ModifyComponent } from './components/views/modify/modify.component';
import { ListComponent } from './components/views/list/list.component';
import { HomeComponent } from './components/views/home/home.component';
import { SearchComponent } from './components/views/search/search.component';
import { AppLoadingComponent } from './components/views/app-loading/app-loading.component';


const routes: Routes = [
  {path: 'add', component: AddComponent},
  {path: 'modify/:id', component: ModifyComponent},
  {path: 'list', component: ListComponent},
  {path: 'search', component: SearchComponent},
  {path: 'loading', component: AppLoadingComponent},
  {path: '', component: HomeComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
