export class Product {

    public idFirebase: string;
    public codigo: string;
    public nombre: string;
    public marca: string;
    public stock: number;
    public precio: number;

    constructor(){}

    public static Create(codigo: string, nombre: string, marca: string, stock: number, precio: number): Product
    {
        let newProduct = new Product();
        
        newProduct.codigo = codigo;
        newProduct.nombre = nombre;
        newProduct.marca = marca;
        newProduct.stock = stock;
        newProduct.precio = precio;

        return newProduct;
    }

}
