import { Component, OnInit } from '@angular/core';
import { Product } from 'src/app/models/product';
import { LocalProductService } from 'src/app/services/firebase/local-product.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

  	public codigo: string;
	public nombre: string;
	public marca: string;
	public stock: number;
	public precio: number;

	public warning : boolean; 

	//private service: ProductService
  	constructor(private service: LocalProductService, private toast: ToastrService) { }

 	ngOnInit() {
 	}

  	public add(): void {
		let product = Product.Create(this.codigo, this.nombre, this.marca, this.stock, this.precio);
		this.service.add(product)
		.then(wng  =>{
			if(wng)
				this.toast.error('Error en el codigo', 'Error');
			else
				this.toast.info('Creado con exito');
		});
	}
}
