import { Injectable } from '@angular/core';
import { AngularFirestore } from 'angularfire2/firestore';
import { Product } from 'src/app/models/product';
import { CoreHelper } from 'src/app/classes/helpers/core-helper';

@Injectable({
	providedIn: 'root'
})
export class LocalProductService {

	constructor(private db: AngularFirestore) { }

	public add(product: any): Promise<boolean>{
		let obj = CoreHelper.convertToObject(product);
		return this.getIdByFireBase(product.codigo, 1).then(prd => {
			if(!prd)
			{
				this.db.collection('products').add(obj);
				return false;
			}
			else
				return true;
		});
	}

	public getAll() {
		return this.db.collection('products');
	}
	
	public getAll_sync(): Promise<Product[]> {
        return this.db.collection('products').get().toPromise().then(query => {
            let prods: Product[] = [];
            query.docs.forEach(doc => {
                let pr = new Product();
                pr = doc.data() as Product;
                pr.idFirebase = doc.id;
                prods.push(pr);
            });
            return prods;
        });
	}
	
	public delete(id: string): void {
		this.db.collection('products').doc(id).delete();
	}

	public modify(codigo: string, obj: any): void {
		obj = CoreHelper.convertToObject(obj);
		this.getIdByFireBase(codigo, 1).then(firebaseId => {
			this.db.collection('products').doc(firebaseId).update(obj)
		})
	}

	public getIdByFireBase(date: string, option: number): Promise<string> {
		switch(option)
		{
			case 1:
				return this.db.collection('products', ref => ref.where('codigo', '==', date))
					.get()
					.toPromise()
					.then(query => {
						return query.docs[0].id;
					})
					.catch(() => {
						return null;
					})
			case 2:
				return this.db.collection('products', ref => ref.where('nombre', '==', date))
					.get()
					.toPromise()
					.then(query => {
						return query.docs[0].id;
					})
					.catch(() => {
						return null;
					})
			case 3:
				return this.db.collection('products', ref => ref.where('marca', '==', date))
					.get()
					.toPromise()
					.then(query => {
						return query.docs[0].id;
					})
					.catch(() => {
						return null;
					})
		}
	}
	
	public getProductByFireBase(idFirebase: string){
		return this.db.collection('products').doc(idFirebase)
			.get()
			.toPromise()
			.then(query => {
                return query.data() as Product;
			})
	}

	public searchProduct(data: string, option: number) {
		return this.getIdByFireBase(data, option)
		.then(id =>{
			return this.getProductByFireBase(id)
			.then(prd =>{
				prd.idFirebase = id;
				return prd;
			});
		});
	}

}
