import { Component, OnInit, OnDestroy } from '@angular/core';
import { Product } from 'src/app/models/product';
import { LocalProductService } from 'src/app/services/firebase/local-product.service';
import { map } from 'rxjs/operators';
import { Subscription } from 'rxjs';

@Component({ 
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit, OnDestroy{
  
	public productList: any;
	public isLoading: boolean;
	private productSubscription: Subscription;

	constructor(private service: LocalProductService) { }

	ngOnInit() {
		this.initializeList();
	}

	ngOnDestroy() {
		this.productSubscription.unsubscribe();
	}

	private initializeList(): void {
		this.isLoading = true;
		this.productList = this.service.getAll().snapshotChanges();
		this.productSubscription = this.productList.subscribe(()  =>{
			this.isLoading = false;
			this.productList = this.service.getAll().snapshotChanges()
			.pipe(
				map(x => x.map(z => {
					let prd: Product = z.payload.doc.data() as Product;
					prd.idFirebase = z.payload.doc.id;
					return prd;
				}))
			)
		})
	}

	public delete(id: string): void {
		this.service.delete(id);
	}
}
