import { Component, OnInit, OnDestroy } from '@angular/core';
import { LocalProductService } from 'src/app/services/firebase/local-product.service';
import { Product } from 'src/app/models/product';

@Component({
	selector: 'app-search',
	templateUrl: './search.component.html',
	styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

	public productList: any;
	
	public isLoading: boolean;

 	public codigo: string;
	public nombre: string;
	public marca: string;

	constructor(private service: LocalProductService) { }

	ngOnInit() {
	}

	public getProduct(option: number) {
		this.isLoading = true;
		this.productList = [];
		if(option == 1)
			this.service.searchProduct(this.codigo, option)
			.then(product => {
				this.productList.push(product);
				this.isLoading = false;
			});
		else if(option == 2)
			this.service.searchProduct(this.nombre, option)
			.then(product => {
				this.productList.push(product);
				this.isLoading = false;
			});
		else if(option == 3)
			this.service.searchProduct(this.marca, option)
			.then(product => {
				this.productList.push(product);
				this.isLoading = false;
			});
	}
}
