import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LocalProductService } from 'src/app/services/firebase/local-product.service';
import { Product } from 'src/app/models/product';

@Component({
  selector: 'app-modify',
  templateUrl: './modify.component.html',
  styleUrls: ['./modify.component.scss']
})
export class ModifyComponent implements OnInit {

	public idFirebase: string;
 	public codigo: string;
	public nombre: string;
	public marca: string;
	public stock: number;
	public precio: number;

	constructor(private service: LocalProductService, private route: ActivatedRoute) { }

	ngOnInit() {
		this.getId();
		this.productComplete();
	}

	private getId(): void {
		this.idFirebase = String(this.route.snapshot.paramMap.get('id'));
	}

	public productComplete(): void {
		let product = this.service.getProductByFireBase(this.idFirebase)
		.then(product => {
			this.codigo = product.codigo;
			this.nombre = product.nombre;
			this.marca = product.marca;
			this.precio = product.precio;
			this.stock = product.stock;
			this.idFirebase = product.idFirebase;
		});
	}

	public modify(): void {
		let product = Product.Create(this.codigo,this.nombre, this.marca, this.stock,this.precio);
		this.service.modify(this.codigo, product);
	}
}
